@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}
$heavy-weight: 700;
$horizontal-anim-duration: 90s;
$vertical-anim-duration: 4.5s;

.faq {
  position: relative;
  overflow: hidden;

  .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 10px;

    .title {
      @extend %standard-font;
      display: inline;
      font-size: 20px;
      font-weight: $heavy-weight;
      color: $color-title !important;

      @media screen and (min-width: 1440px) {
        font-size: 34px;
      }
    }

    .desc {
      text-align: center;
      @extend %standard-font;
      font-size: $font-size-small;
      font-weight: $font-weight-normal;
      color: $color-title !important;

      @media screen and (min-width: 1440px) {
        font-size: 19.5;
      }
    }
  }
  .content {
  
    .content-section {
      // margin-top: 108px;
      margin-bottom: 108px;
  
      @media screen and (min-width: 1440px) {
        margin-top: 48px;
      }
    }
  
    .content-row {
      justify-content: center;
      margin-bottom: 50px;
      padding: 20px;
  
      @media screen and(min-width: 768px){
        padding: 40px;
      }
    }
  
    .title {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-extra-big;
      }
    }
  
    .title-sec {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-heavy;
      color: $color-title;
      padding:0 10px;
      text-align: left !important;
    }
  
    .title-desc {
      @extend %standard-font;
      font-size: $font-size-smallest;
      color: $color-body;
      width: 100vw;
      padding:0 15px;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
    }
  
    .faq-wrapper {
      padding: 20px 10px;
  
      @media screen and(min-width: 1000px){
        padding: 20px 10%;
      }
  
      .qna-container {
        cursor: pointer;
        padding: 10px;
        
        .nav-item {
          display: flex;
        }

        .nav-link {
          flex: 1;
          display: inline-block;
          color: $color-title;
          background-color: rgba(255, 255, 255, 0);
          border-color: rgba(255, 255, 255, 0);
          padding-left: 0;
          padding-right: 0;
    
          &:hover {
            color: $color-title;
          }
        }

        .arrow {
          display: inline-block;
          float: right;
          width: 20px;
          transition: all 0.3s ease;
        }

        .rotate-arrow-up {
          transform: rotateX(0);
        }

        .rotate-arrow-down {
          transform: rotateX(180deg);
        }
      }
    }
    .faq-inner {
    
      .card {
        border-radius: 0;
        border-top: 2px solid $color-hover;
        border-left: 0;
        border-right: 0;
        border-bottom: 0;
  
        .card-body {
          padding: 10px;
          font-size: $font-size-small;
          color: $color-body;
          letter-spacing: -0.2px;
  
          @media screen and(min-width: 820px) {
            padding: 1.25rem;
          }
        }
      }
  
      ol {
        padding-left: 20px;
  
        li {
          padding: 10px;
        }
      }
    }
  }
}
